import './style.css'

import * as THREE from 'three'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass.js';
import { SMAAPass } from 'three/examples/jsm/postprocessing/SMAAPass.js';



      //Scene
      var scene = new THREE.Scene();

      //Camera
		var height = window.innerHeight;
		var width = window.innerWidth;
		var distance = 1000;
		var diag = Math.sqrt((height*height)+(width*width))
		var fov = 2 * Math.atan((diag) / (2 * distance)) * (180 / Math.PI); //Field of View
		var camera = new THREE.PerspectiveCamera(fov, window.innerWidth / window.innerHeight, 0.1, distance);
		camera.position.set(0, 25, -50);

      //Canvas
      var myCanvas = document.getElementById('webgl');

      //Renderer
      var renderer = new THREE.WebGLRenderer({
        antialias: true,
        canvas: myCanvas,
		alpha: true
      });


		//renderer.setClearColor(0x000000);
		renderer.setPixelRatio(window.devicePixelRatio);
		renderer.setSize(window.innerWidth, window.innerHeight);
    	renderer.antialias = true;
		document.body.appendChild( renderer.domElement );

		const composer = new EffectComposer( renderer );

		let renderPass = new RenderPass( scene, camera );
		composer.addPass( renderPass );

		let effectBloom = new UnrealBloomPass( undefined,undefined,0.2,0.1 );
		composer.addPass( effectBloom );


		//OrbitControls
		const orbit = new OrbitControls(camera, renderer.domElement);
		orbit.maxPolarAngle = Math.PI / 2;
		orbit.target.set( 0, 0, 200 );
		orbit.update();

		// Materials
		const red = new THREE.ShaderMaterial({
			transparent: true,
			blending: THREE.AdditiveBlending,
			depthWrite: false,
			side: THREE.DoubleSide,
			uniforms:
		    {
		        uTime: { value: 0 }
		    },
		    vertexShader: `
		        uniform float uTime;
				varying vec2 vUv;

				void main()
				{
				    gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);

				    vUv = uv;
				}
		    `,
		    fragmentShader: `
				uniform float uTime;

		        varying vec2 vUv;

				
				void main()
				{

				    vec2 _st = fract((vUv)*20.0);
				    vec3 color = vec3(0.0);


				    color = 1.0-smoothstep(0.1, 0.1*1.1,dot(_st-.5,_st-.5)*4.)*vec3(1.0);

				    gl_FragColor = vec4(color,1.0);	

				    gl_FragColor = gl_FragColor * vec4(1.0, 0.,0., 1.0);
				}
			`
		})

		const green = new THREE.ShaderMaterial({
			transparent: true,
			blending: THREE.AdditiveBlending,
			depthWrite: false,
			side: THREE.DoubleSide,
			uniforms:
		    {
		        uTime: { value: 0 }
		    },
		    vertexShader: `
		        uniform float uTime;
				varying vec2 vUv;

				void main()
				{
				    gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);

				    vUv = uv;
				}
		    `,
		    fragmentShader: `
				uniform float uTime;

		        varying vec2 vUv;

				
				void main()
				{

				    vec2 _st = fract((vUv)*20.0);
				    vec3 color = vec3(0.0);


				    color = 1.0-smoothstep(0.1, 0.1*1.1,dot(_st-.5,_st-.5)*4.)*vec3(1.0);

				    gl_FragColor = vec4(color,1.0);	

					gl_FragColor = gl_FragColor * vec4(0.0, 1.,0., 1.0);

				}
			`
		})

		const blue = new THREE.ShaderMaterial({
			transparent: true,
			blending: THREE.AdditiveBlending,
			depthWrite: false,
			side: THREE.DoubleSide,
			uniforms:
		    {
		        uTime: { value: 0 }
		    },
		    vertexShader: `
		        uniform float uTime;
				varying vec2 vUv;

				void main()
				{
				    gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);

				    vUv = uv;
				}
		    `,
		    fragmentShader: `
				uniform float uTime;

		        varying vec2 vUv;

				
				void main()
				{

				    vec2 _st = fract((vUv)*20.0);
				    vec3 color = vec3(0.0);


				    color = 1.0-smoothstep(0.1, 0.1*1.1,dot(_st-.5,_st-.5)*4.)*vec3(1.0);

				    gl_FragColor = vec4(color,1.0);	

				    gl_FragColor = gl_FragColor * vec4(0.0, 0.,1., 1.0);


				}
			`
		})

		const white = new THREE.ShaderMaterial({
			transparent: true,
			blending: THREE.AdditiveBlending,
			depthWrite: false,
			side: THREE.DoubleSide,
			uniforms:
		    {
		        uTime: { value: 0 }
		    },
		    vertexShader: `
		        uniform float uTime;
				varying vec2 vUv;

				void main()
				{
				    gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);

				    vUv = uv;
				}
		    `,
		    fragmentShader: `
				uniform float uTime;

		        varying vec2 vUv;

				
				void main()
				{

				    vec2 _st = fract((vUv)*15.0);
				    vec3 color = vec3(0.0);


				    color = 1.0-smoothstep(0.1, 0.1*1.1,dot(_st-.5,_st-.5)*4.)*vec3(1.0);

				    gl_FragColor = vec4(color,1.0);	


				}
			`
		})

		const rainbow = new THREE.ShaderMaterial({
			transparent: true,
			blending: THREE.AdditiveBlending,
			depthWrite: false,
			side: THREE.DoubleSide,
			uniforms:
		    {
		        uTime: { value: 0 }
		    },
		    vertexShader: `
		        uniform float uTime;
				varying vec2 vUv;

				void main()
				{
				    gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);

				    vUv = uv;
				}
		    `,
		    fragmentShader: `
				uniform float uTime;

		        varying vec2 vUv;

				
				void main()
				{

				    vec2 _st = fract((vUv)*30.0);
				    vec3 color = vec3(0.0);


				    color = 1.0-smoothstep(0.04, 0.04*1.1,dot(_st-.5,_st-.5)*4.)*vec3(1.0);

				    gl_FragColor = vec4(color,1.0);	

				    gl_FragColor = gl_FragColor * vec4(vUv+0.5*sin(uTime),1., 1.0);



				}
			`
		})

		// Instantiate a loader
		let mixer;
		var loader = new GLTFLoader();
		loader.load('/3d/basebal.glb',

			(gltf) => {

			// Add textures and set up animated objects
		    gltf.scene.traverse(object => {

				const black = new THREE.MeshBasicMaterial({color: 0x000000})

				const base = new THREE.MeshBasicMaterial({color: 0x333333})

		        if(object.type === 'Mesh') {
		          object.material = base;
		        }

		        if (object.name.includes("red")) {
		         	object.material = red;
		        }
		        if (object.name.includes("blue")) {
		         	object.material = blue;
		        }
		        if (object.name.includes("white")) {
		         	object.material = white;
		        }
		        if (object.name.includes("green")) {
		         	object.material = green;
		        }
		        if (object.name.includes("rainbow")) {
		         	object.material = rainbow;
		        }
		        if (object.name.includes("red")) {
		         	object.material = red;
		        }


		        
		        if (object.name.includes("black")) {
		         	object.material = black;
		        }

		    });

		      //This section only triggers if load was successful
		      gltf.scene.scale.set(1,1,1) // scale entire scene
		      gltf.scene.position.set(0,0,0) // pos entire scene
		      gltf.scene.rotation.y = 4; // pos entire scene
		      scene.add(gltf.scene)

		      let animations = gltf.animations;
			  	console.log(gltf.animations[0]);
				if ( animations && animations.length ) {
		            mixer = new THREE.AnimationMixer( gltf.scene );
		            for ( let i = 0; i < animations.length; i ++ ) {
		                let animation = animations[ i ];
		                mixer.clipAction( animation ).play();
		            }
		        }

		  },
		  		  // called when loading is in progresses
		  function ( xhr ) {

		    console.log( ( xhr.loaded / xhr.total * 100 ) + '% loaded' );

		  },
		  // called when loading has errors
		  function ( error ) {

		    console.log( 'An error happened');
		    console.log( error );

		  }
		);


	const clock = new THREE.Clock();

	//Render loop
	render();

	function render() {
		const time = clock.getElapsedTime();
		rainbow.uniforms.uTime.value = time;
		if(mixer){mixer.update( 1/120 );}
		//exposure
		composer.render();

		requestAnimationFrame(render);
	}


